<template>
    <div>
        <div id="particles_con"></div>
        <img src="../../assets/logo.jpg" class="logo"/>
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>GM</span>
                <el-button style="float: right; padding: 3px 0" type="text"
                           @click="handleChangeStatus">{{isLogin ? '' : ''}}
                </el-button>

            </div>
            <div style="textAlign:center;">
                <div v-show="isLogin">
                    <el-form :model="ruleFormlogin" status-icon :cpuOptions="ruleLogin"
                             ref="ruleFormlogin" label-width="80px">
                        <el-form-item label="" prop="account">
                            <el-input v-model="ruleFormlogin.account"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input type="password" v-model="ruleFormlogin.password"
                                      autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item style="textAlign:right;">
                            <el-button type="primary" @click="submitFormLogin('ruleFormlogin')">
                            </el-button>
                            <el-button type="primary" @click="showUpdatePassword">
                                
                            </el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <div v-show="!isLogin">
                    <el-form :model="ruleFormRegist" status-icon :rules="ruleRegist"
                             ref="ruleFormRegist" label-width="80px">
                        <el-form-item label="" prop="account">
                            <el-input v-model="ruleFormRegist.account"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input type="password" v-model="ruleFormRegist.password"
                                      autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="checkPass2">
                            <el-input type="password" v-model="ruleFormRegist.checkPass2"
                                      autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="name">
                            <el-input v-model="ruleFormRegist.name"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="email">
                            <el-input v-model="ruleFormRegist.email"></el-input>
                        </el-form-item>
                        <el-form-item style="textAlign:right;">
                            <el-button type="primary" @click="submitFormRegist('ruleFormRegist')">
                                
                            </el-button>

                        </el-form-item>
                    </el-form>
                </div>
                <el-dialog  width="30%"  title="" :visible.sync="updatePasswordDisplay">
                    <el-form :model="ruleFormUpdate" status-icon :rules="ruleRegist"
                             ref="ruleFormRegist" label-width="80px">
                        <el-form-item label="" prop="account">
                            <el-input v-model="ruleFormUpdate.account"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="password">
                            <el-input type="password" v-model="ruleFormUpdate.password"
                                      autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="" prop="newPassword">
                            <el-input type="password" v-model="ruleFormUpdate.newPassword"
                                      autocomplete="off"></el-input>
                        </el-form-item>

                        <el-form-item style="textAlign:right;">
                            <el-button type="primary" @click="submitUpdate()">
                                
                            </el-button>
                        </el-form-item>
                    </el-form>

                </el-dialog>


            </div>
        </el-card>
    </div>
</template>

<script>
import md5 from 'js-md5';
import 'particles.js/particles.js';
import particlesOptions from '@/libs/particles';

export default {
    name: 'login',
    data() {
        let validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                var tx = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[^]{8,16}$/;

                if (!tx.test(value)) {
                    callback(new Error('8-16'));
                }
                if (this.ruleFormRegist.checkPass2) {
                    this.$refs.ruleFormRegist.validateField('checkPass2');
                }
                callback();
            }
        };
        let validatePassCheck = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.ruleFormRegist.password) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            autoLogin: true,
            isLogin: true,
            ruleFormlogin: {account:'', password:''},
            ruleFormRegist: {},
            ruleFormUpdate: {},
            updatePasswordDisplay: false,
            ruleLogin: {
                account: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                },
                {
                    min: 4,
                    max: 20,
                    message: ' 4  20 ',
                    trigger: 'blur'
                }
                ],
                password: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                },
                {
                    min: 4,
                    max: 20,
                    message: ' 4  20 ',
                    trigger: 'blur'
                }
                ]
            },
            ruleRegist: {
                account: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                },
                {
                    min: 4,
                    max: 20,
                    message: ' 4  20 ',
                    trigger: 'blur'
                }
                ],
                password: [{
                    validator: validatePass,
                    required: true,
                    trigger: 'blur'
                },
                {
                    min: 8,
                    max: 16,
                    message: ' 8  16 ',
                    trigger: 'blur'
                }
                ],
                newPassword: [{
                    validator: validatePass,
                    required: true,
                    trigger: 'blur'
                },
                {
                    min: 8,
                    max: 16,
                    message: ' 8  16 ',
                    trigger: 'blur'
                }
                ],
                checkPass2: [{
                    validator: validatePassCheck,
                    required: true,
                    trigger: 'blur'
                },
                {
                    min: 8,
                    max: 16,
                    message: ' 8  16 ',
                    trigger: 'blur'
                }
                ],
                name: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }],
                email: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                },
                {
                    type: 'email',
                    message: '',
                    trigger: ['blur', 'change']
                }
                ]
            }
        };
    },
    methods: {
        getHeader(){
            this.$fetch.post(API.app.get_header).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    document.title = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        submitFormRegist(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.ruleFormRegist));
                    delete objCopy.checkPass2;
                    let password = objCopy.password;

                    objCopy.password = md5(password);
                    this.$fetch.post(API.app.regist, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            this.isLogin = true;
                            this.$refs[formName].resetFields();
                        } else {
                            this.$message.error(res.desc);
                        }
                    });
                } else {
                    return false;
                }
            });
        },
        submitFormLogin(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    let objCopy = JSON.parse(JSON.stringify(this.ruleFormlogin));
                    let password = objCopy.password;
                    objCopy.password = md5(password);
                    this.$fetch.post(API.app.login, objCopy).then(response => {
                        let res = response.data;
                        if (res.status == 200) {
                            let userInfo = res.data;
                            sessionStorage.setItem('status', Date.parse(new Date()));
                            sessionStorage.setItem('userInfo', JSON.stringify(userInfo));

                            this.$refs[formName].resetFields();
                            let route = {};
                            if (userInfo.powerType == 1){
                                route.name = 'server_charts';
                            }else if (userInfo.powerType == 2) {
                                route.name = 'serverManager';
                            }else if (userInfo.powerType == 3) {
                                route.name = 'role';
                            }else if (userInfo.powerType == 4) {
                                route.name = 'role';
                            }else if (userInfo.powerType == 0) {
                                route.name = 'serverManager';
                            }else {
                                route.name = userInfo.menuList[0];
                            }

                            this.$router.push(route);
                        } else {
                            if (!this.autoLogin){
                                this.$message.error(res.desc);
                            }
                        }
                        this.autoLogin = false;
                    });
                } else {
                    return false;
                }
            });
        },
        handleChangeStatus() {
            this.isLogin = !this.isLogin;
        },
        showUpdatePassword(){
            this.updatePasswordDisplay = true;
        },
        submitUpdate(){
            let objCopy = JSON.parse(JSON.stringify(this.ruleFormUpdate));
            let password = objCopy.password;
            objCopy.password = md5(password);
            objCopy.newPassword = md5(objCopy.newPassword);
            this.$fetch.post(API.app.update_password, objCopy).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.$message.success(res.desc);
                } else {
                    this.$message.error(res.desc);
                }
            });
        },
        ssoLogin(){
            this.$fetch.post(API.app.sso_login).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let userInfo = res.data;
                    sessionStorage.setItem('status', Date.parse(new Date()));
                    sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
                    let route = {};
                    if (userInfo.powerType == 1){
                        route.name = 'server_charts';
                    }else if (userInfo.powerType == 2) {
                        route.name = 'serverManager';
                    }else if (userInfo.powerType == 3) {
                        route.name = 'role';
                    }else if (userInfo.powerType == 4) {
                        route.name = 'role';
                    }else if (userInfo.powerType == 0) {
                        route.name = 'serverManager';
                    }else {
                        route.name = userInfo.menuList[0];
                    }
                    this.$router.push(route);
                } else {
                    this.ruleFormlogin.account = 'root';
                    this.ruleFormlogin.password = 'Pwrd123456';
                    this.submitFormLogin('ruleFormlogin');
                    this.ruleFormlogin.account = '';
                    this.ruleFormlogin.password = '';
                }
            });
        }

    },
    computed: {},
    mounted() {
        particlesJS('particles_con', particlesOptions);
        sessionStorage.setItem('status', Date.parse(new Date()));
        let temp = {
            name: "admin",
            powerType: 1,
            account: "admin",
            menuList: ["log"],
            powerTypeName: ""
        }
        sessionStorage.setItem('userInfo', JSON.stringify(temp));
        let route = {};
        route.name = 'log';
        this.$router.push(route);
    }
};


</script>

